import React from 'react';
import styles from "./css/Navigation.module.css";
import { NavLink } from "react-router-dom";

const NavLinks = () => {
    return (
    <ul className={styles.NavIn}>
        <li className={styles.NavButton}><NavLink to="/HomePage">Home</NavLink></li>
        <li className={styles.NavButton}><NavLink to="/SeasonPage">Season</NavLink></li>
        <li className={styles.NavButton}><NavLink to="/PhotosPage">Photos</NavLink></li>
        <li className={styles.NavButton}><NavLink to="/ContactPage">Contact</NavLink></li>
    </ul>
    );
};

export default NavLinks;