import React from 'react';
import styles from "./css/Seasons.module.css";

const Item = [
    {id: 1, title:'1-2 January', description:'VDC Blackwood'},
    {id: 2, title:'2-3 January', description:'DCGP Blackwood'},
    {id: 3, title:'4-5 January', description:'WDT Blackwood'},
    {id: 4, title:'5-6 January', description:'VDC Blackwood'},
    {id: 5, title:'7-8 January', description:'DCGP Blackwood'},
]
let Stages = Item.map(function (props){
    return (
      <div className={styles.ContainerItem}>
          <div className={styles.SeasonTitle} key={props.id}>
              {props.title}
          </div>
          <div className={styles.SeasonDesc}>
              {props.description}
          </div>
      </div>
    );
})
const SeasonItem = () => {
    return (
        <div className={styles.ContainerWrap}>
            {Stages}
        </div>
    );
};

export default SeasonItem;